.app {
    height: 100vh;
    background: black;
}

.bfc{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 80vh;
    color: white;
}

.mint {
    border: 0.5px solid white;
    border-radius: 10px;
    padding: 10px;

    box-shadow: 0px 10px 20px 2px #f86565;
}

.form-control:valid {
    background-color: #101d21 !important;
    color: white;
}

.coming-soon-modal {
    height: 10rem;
}

.bfc-btn {
    background-color: #f86565;
    color: white;
    border: 0.5px solid white;
    border-radius: 10px;
    padding: 10px;
    margin-top: 10px;
    width: 15rem;
    margin-bottom: 20px;
    cursor: pointer;
}

.btn-wallet {
    background-color: #f86565!important;
}

.custom-control-input:checked~.custom-control-label::before {
    color: #fff!important;
    border-color: #7B1FA2!important;
}

.custom-control-input:checked~.custom-control-label.red::before {
    background-color: #f86565!important;
}

.custom-control-input:checked~.custom-control-label.green::before {
    background-color: green!important;
}